import common from '~/mixins/common.js'

export default {
  name: 'ClickableImage',
  mixins: [common],
  props: {
    name: {
      type: String,
      required: true,
    },
    date: {
      type: String,
      required: false,
      default: null,
    },
    img: {
      type: String,
      required: true,
    },
    link: {
      type: String,
      required: true,
    },
    trapezoid:{
      type: Boolean,
      required:false,
      default: false,
    }

  },
}
