import clickableImage from '~/components/informative/ClickableImage.vue'
export default {
  name: 'ShowcaseComponent',
  components: {
    clickableImage,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    linkName: {
      // The name displayed on the button on the right
      type: String,
      required: false,
      default: 'See all',
    },
    linkPath: {
      // Link of the button on the right
      type: String,
      required: true,
    },
    content: {
      type: Array, // Array of at least 4 elements with title, images array, (date) [only the first image of the array will be used]
      required: true,
    },
    isLeft: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  mounted() {},
  methods: {
    titleClass(isLeft) {
      if (isLeft) return 'left'
      return 'right'
    },
    outer(isLeft) {
      if (isLeft) return 'outerLeft'
      return 'outerRight'
    },
  },
}
